@import url(https://fonts.googleapis.com/css2?family=Arsenal:ital,wght@0,400;0,700;1,400;1,700&display=swap);
html {
  font-size: calc(5px + 1.2vw);
  font-family: -apple-system, 'Helvetica Neue', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arsenal, -apple-system, 'Helvetica Neue', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  background-color: hsla(230, 30%, 50%, 0.17);
}

* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

.Button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: 0;
  border-radius: 0.2rem;

  display: inline-block;
  min-width: 5rem;
  padding: 0.25rem 0.9rem;
  font-family: -apple-system, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  cursor: pointer;
  font-size: 1rem;
  box-shadow: 0.15rem 0.15rem 0.2rem rgba(0, 0, 0, 0.25);

  background: hsla(230, 20%, 50%, 0.7);
  color: #fff;
}

.Button:hover {
  background: hsla(230, 20%, 50%, 1);
}

.Button:focus {
  outline: none;
}

.App {
  padding: 0 1em;
}

.Parts {
  display: flex;
  flex-flow: wrap;
  justify-content: center;
  text-align: left;
}

.Part {
  flex-grow: 1 1 auto;
  padding: 0 1em 0.3em 1em;
  min-width: 30%;
  margin: 0;
}

.PartFull {
  width: 95%;
}

.PartLeft {
  max-width: 35%;
  background-color: #fff;
  border-radius: 0.2em;
  box-shadow: 0.2rem 0.2rem 0.5rem rgba(0, 0, 0, 0.25);
}

.PartRight {
  max-width: 65%;
  padding-left: 1.5em;
}

@media (max-width: 760px) {
  /*
   * for small screen all parts go in one column
   */

  .PartLeft {
    max-width: 95%;
  }

  .PartRight {
    max-width: 95%;
  }
}

@media (min-width: 1440px) {
  .PartLeft {
    min-width: 30%;
    max-width: 30%;
  }

  .PartRight {
    min-width: 60%;
    max-width: 60%;
  }
}

/* Accordion */

.Accordion {
  /* border-radius: 0.2em; */
  /* border: solid 1px #ccc; */
  background-color: hsla(230, 30%, 70%, 0.2);
  /* margin: 0.1em; */
  padding: 0.4em;
  text-align: left;
}

.AccordionSection {
  /* border-radius: 0.2em; */

  border-color: #888;
  border-style: solid;
  border-width: 0.05em;

  background-color: hsla(230, 30%, 50%, 0.17);
  background-color: white;
  margin: 0.4em 0.4em;
  padding: 0.2em;
}

.Element {
  border-color: white;
  border-style: dashed;
  border-width: 0.05em;
  margin: 0.1em;
  padding: 0.1em 0.2em;
  background-color: hsla(230, 30%, 50%, 0.17);
}

.BodyElement {
  margin: 0.4em 0.2em;
  background-color: hsla(50, 100%, 50%, 0.27);
  padding: 0.4em;
}

.Highlight {
  font-size: 0.9em;
  color: #c03;
}

.Explain {
  margin: 0.2em 0 0.5em 2em;
  font-size: 0.9em;
}

